@import "./App.scss";

.description-container {
  display: flex;
  justify-content: flex-end;
  margin: 5rem;
  .description {
    width: 85%;
    font-family: "Merriweather", serif;
    font-weight: 300;
    line-height: 200%;
    letter-spacing: 0.15em;
    p {
      margin-bottom: 1rem;
    }
  }
  .colored-indent {
    width: 26%;
    hr {
      height: 26px;
      border: none;
      float: right;
      background-color: #f9ea9a;
      width: 70px;
      margin-right: 30px;
      margin-top: 8px;
    }
  }
}
